<template>
  <div class="center">
    <h2>{{error}}</h2>
    <form class="sign">
      <div class="sign__text">
        <h2>Регистрация</h2>
      </div>
      <div class="sign__item">
        <div class="sign__top">
          <input type="email" required placeholder="Введите email" v-model="email" :rules="emailRules" />
        </div>
        <div class="sign__bottom">
          <input type="password" required placeholder="Введите пароль" v-model="password" :rules="passwordRules" />
        </div>
        <div class="sign__button">
          <a href='/' class="btn" v-on:click.prevent="signup">Зарегистрироваться</a>
        </div>
      </div>
    </form>
  </div>
</template>

<script>
  export default {
    data() {
      return {
        email: null,
        password: null,
        valid: false,
        emailRules: [
          v => !!v || 'Пожалуйста введите email',
          v => /^([a-z0-9_-]+\.)*[a-z0-9_-]+@[a-z0-9_-]+(\.[a-z0-9_-]+)*\.[a-z]{2,6}$/.test(v) || 'Неправильный email'
        ],
        passwordRules: [
          v => !!v || 'Пожалуйста введите пароль',
          v => v && v.length >=6 || 'Пароль слишком короткий - минимум 6 символов'
        ]
      }
    },
    computed: {
      error() {
        return this.$store.getters.getError
      },
      processing() {
        return this.$store.getters.getProcessing
      },
      isUserAuthenticated() {
        return this.$store.getters.isUserAuthenticated
      }
    },
    watch: {
      isUserAuthenticated(value) {
        if (value === true) {
          this.$router.push('/')
        }
      }
    },
    methods: {
      signup() {
        this.$store.dispatch('SIGNUP', {
          email: this.email,
          password: this.password
        })
      }
    }
  }
</script>

<style lang="scss" scoped>
  .sign {
    background: #fff;
    width: 800px;
    height: 500px;
    margin: 0 auto;
    text-align: center;
    border-radius: 5px;
    border: 1px solid #eee;
    &__text {
      display: flex;
      height: 20vh;
      justify-content: center;
      align-items: center;
      h2 {
        font-size: 26px;
        letter-spacing: 5px;
        text-transform: uppercase;
        font-weight: 300;
      }
    }
    &__top {
      margin-bottom: 30px;
      margin-top: 80px;
    }
    &__top, &__bottom {
      input {
        cursor: pointer;
        width: 0px;
        height: 40px;
        padding: 0px 5px;
        border: none;
        outline: none;
        border-bottom: 1px solid #000;
        transition: .3s;
        &:hover, &:focus {
          width: 300px;
        }
      }
    }
    &__bottom {
      margin-bottom: 60px;
    }
    .btn {
      display: block;
      width: 300px;
      height: 50px;
      border: 1px solid #000;
      margin: 0 auto;
      line-height: 50px;
      color: #000;
      font-size: 16px;
      text-decoration: none;
      font-weight: 300;
      text-transform: uppercase;
      border-radius: 50px;
      transition: .3s;
      &:hover {
        background: #000;
        color: #fff;
      }
    }
  }

  .center {
    display: flex;
    height: 70vh;
    align-items: center;
  }
</style>
